                                color: function (params){
                                    var colorList = ['#C33531','#EFE42A','#64BD3D','#EE9201','#29AAE3', '#B74AE5','#0AAF9F','#E89589','#16A085','#4A235A','#C39BD3 ','#F9E79F','#BA4A00','#ECAAAA','#616A6B','#EACCCC','#4A235A','#3498DB' ];
